//------------------------------------------------------------------
// Splash Loader
//------------------------------------------------------------------

.ss-loading-screen:before {
  display: none;
}

.spinner {
  width: 200px;
  text-align: center;

  div {
    width: 30px;
    height: 30px;
    background-color: $highlight;
    border-radius: 100%;
    display: inline-block;
    animation: sk-bouncedelay 1.7s infinite ease-in-out both;
    margin-right: 5px;
  }
}

.spinner .spinner__item1 {
  animation-delay: -.60s;
}

.spinner .spinner__item2 {
  animation-delay: -.40s;
}

.spinner .spinner__item3 {
  animation-delay: -.20s;
}

@keyframes sk-bouncedelay {
  0%, 80%, 100% {
    transform: scale(0);
    opacity: 0;
  }

  40% {
    transform: scale(1);
    opacity: 1;
  }
}